<template>
  <BaseLayout title="礼品卡">
    <template #extra>
      <van-button icon="add" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" @click="state.popup=true">开卡</van-button>
    </template>

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <div v-for="(item, index) in state.list" :key="index">
          <van-tag class="dt" color="#CCC">{{item.dt}}</van-tag>
          <van-cell-group class="item" :border="false">
            <van-cell>
              <template #title>
                <div class="line1" :class="{used: item.status == 1}">￥ {{item.value}}</div>
              </template>
              <template #value>
                <div class="id">#{{item.id}}</div>
                <van-icon name="gold-coin-o" size="12" @click="$copy(item.token)"> 复制卡号</van-icon>
                &nbsp;
                <van-icon name="share-o" size="12" @click="onShare(item.token)"> 分享</van-icon>
              </template>
            </van-cell>
            <van-cell class="line2" :title="item.remark" :border="false"><van-icon :name="item.status == 1 ? 'checked' : 'circle'" @click="showRemark(item)"> 备注</van-icon></van-cell>
          </van-cell-group>
        </div>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>

    <van-popup v-model:show="state.popup" round closeable position="bottom">
      <div class="form">
        开卡
        <van-field type="number" :border="true" v-model="state.amount" input-align="right" clearable label="面值" placeholder="卡的面值金额"></van-field>
        <van-field type="number" :border="true" v-model="state.num"    input-align="right" clearable label="数量" placeholder="要开卡的数量"></van-field>
        <van-field v-model="state.remark" type="text" maxlength="20" input-align="right" label="备注" placeholder="可以不填" />
        <van-button round block type="primary" :style="{margin: '10px 0'}" @click="onCreate">立即开卡</van-button>
      </div>
    </van-popup>

    <van-popup v-model:show="state.dialog1"><van-image class="giftcard" :src="state.giftcard" width="300"><br/>长按图片保存到手机或分享给好友</van-image></van-popup>

    <van-dialog v-model:show="state.dialog2" theme="round-button" show-cancel-button @confirm="gotoRemark">
      <van-notice-bar left-icon="info-o">系统不提供匿名卡的任何使用信息，请自行备注卡的使用情况。</van-notice-bar>
      <van-field v-model="state.content" rows="2" autosize type="textarea" maxlength="100" placeholder="请备注卡的使用情况" show-word-limit/>
      <van-radio-group v-model="state.checked" direction="horizontal" :style="{margin:'15px',justifyContent:'space-around'}">
        <van-radio name="0">未使用</van-radio>
        <van-radio name="1">已使用</van-radio>
      </van-radio-group>
    </van-dialog>

  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
  text-align: center;
}

.item {
  height: 120px;
  margin: 5px;
  background: #FFF;
  border-radius: 5px;
  text-align: left;
  overflow:hidden;
}
.dt {
  margin: 10px 0 5px 0;
}
.id {
  margin-left: 20px;
  color: #999;
  font-size: 12px;
}
.line1 {
  padding-top: 10px;
  color: #0C0;
  font-size: 24px;
  font-weight: 600;
}
.line2 {
  color: #999;
  font-size: 12px;
}
.used {
  color: #999;
}

.form {
  margin: 15px 10px;
  text-align: center;
}

.giftcard {
  margin: 10px;
  font-size: 12px;
  text-align: center;
}
</style>

<script>
import {reactive} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup() {
    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,
      grid: true,
      list: [],
      offset: 0,
      limit : 20,

      popup: false,
      amount: 100,
      num: 1,
      remark: "",

      giftcard: "",

      dialog: false,
      id: 0,
      content: "",
      checked: 0,
    });
    return {state};
  },

  created() {
    this.onRefresh();
  },

  methods: {
    onRefresh() {
      this.state.popup = false;
      this.state.dialog1 = false;
      this.state.dialog2 = false;

      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/team/cardList?offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push(item);
        }
      });
    },
    onCreate() {
      this.$http.post("/team/cardCreate?amount=" + this.state.amount + "&num=" + this.state.num + "&remark=" + this.state.remark).then((data) => {
        this.$copy(data.token);
        this.onRefresh();
        this.$dialog.alert({title:"开卡成功", message: "卡号已复制到剪贴板", theme: "round-button"});
      });
    },

    onShare(token) {
      this.$http.post("/wechat/code?scene=" + token).then((data) => {
        let qrcode = "http://mp.weixin.qq.com/cgi-bin/showqrcode?ticket=" + data.ticket;
        console.log(qrcode);
        this.state.giftcard = this.$host + "/mall/card?type=gift&qrcode=" + encodeURIComponent(qrcode);
        this.state.dialog1 = true;
      });
    },

    showRemark(item) {
      this.state.id = item.id;
      this.state.content = item.remark;
      this.state.checked = item.status;
      this.state.dialog2 = true;
    },
    gotoRemark() {
      this.$http.post("/team/cardRemark?id=" + this.state.id + "&status=" + this.state.checked + "&content=" + this.state.content).then(this.onRefresh);
    },
  }
}
</script>
